<template>
  <div>
    <div v-if="!postLoading">
      <div class="row container d-flex flex-lg-row flex-column flex-sm-column">
        <div class="col mx-3">
          <h2>{{ beer.name }}</h2>
          <p><strong>Tagline: </strong>{{ beer.tagline }}</p>
          <p><strong>Tips: </strong>{{ beer.brewers_tips }}</p>
          <p><strong>Description: </strong> {{ beer.description }}</p>
          <p><strong>ABV:</strong> {{ beer.abv }} <strong>IBU:</strong> {{ beer.ibu }}</p>
          <p><strong>Food pairing: </strong></p>
          <ul id="paring">
            <li v-for="food in beer.food_pairing" :key="food">
              {{ food }}
            </li>
          </ul>
        </div>
        <div class="col d-flex justify-content-center">
          <img class="postImg" :src="`${beer.image_url}`" />
        </div>
      </div>
    </div>
    <div v-else><p>Loading</p></div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      beer: {},
      id: "",
      postLoading: true,
    };
  },
  methods: {
    getId() {
      const source = window.location.pathname.replace(/[^\d]/g, "");
      this.id = source;
    },
    async fetchExectBeer() {
      try {
        const res = await axios.get(`https://api.punkapi.com/v2/beers/${this.id}`, {});
        this.beer = res.data[0];
      } catch (e) {
        console.error(e);
      } finally {
        this.postLoading = false;
      }
    },
  },
  mounted() {
    this.getId();
    this.fetchExectBeer();
  },
};
</script>

<style scoped>
.postImg {
  max-height: 60vh;
}
</style>
