            developed in Britain and transferred to the Continent, where the development
            of ice-making and refrigeration equipment in the late 19th century enabled
            lager beers to be brewed in summer. In the 1860s the French chemist Louis
            Pasteur, through his investigations of fermentation, established many of the
            microbiological practices still used in brewing. The Danish botanist Emile
            Hansen devised methods for growing yeasts in cultures free of other yeasts and
            bacteria. This pure-culture technology was taken up quickly by Continental
            lager brewers but not until the 20th century by the ale brewers of Britain.
            Meanwhile, German-style lagers bottom-fermented by pure yeast cultures became
            dominant in the Americas.
          </p>
          <p class="text-start">
            Brewing in the 21st century is a large-scale industry. Modern breweries use
