          </p>
          <p class="text-start">
            In Europe the properties of the water used for brewing, the types of malt, the
            brewing practices, and the yeast strains have contributed to traditional
            distinctions between beers. Early British beers were made from successive
            extracts of a single batch of brown malt in a top-fermentation process. The
            first and strongest extract gave the best-quality beer, called strong beer,
            and a third extract yielded the poorest-quality beer, called small beer. In
            the 18th century London brewers departed from this practice and produced
            porter. Made from a mixture of malt extracts, porter was a strong,
            dark-coloured, highly hopped beer consumed by the market porters in London.
            Brewers in Burton upon Trent, using the famous hard waters of that region and
            pale malts roasted in coke-fired kilns, created pale ales, also called best
            bitter. Pale ale is less strong, less bitter, paler in colour, and clearer
            than porter. Mild ales—weaker, darker, and sweeter than bitter—are a common
            variation; more colour is obtained by special malts, roasted barley, or
            caramels, less hops are used, and cane sugar is added to impart sweetness and
            aid maturation. Stouts are stronger versions of mild ale; some, such as milk
            stouts, contain lactose (milk sugar) as a sweetener. Beers with alcohol
            content well in excess of 5 percent are produced in the United Kingdom (barley
            wines), Belgium, and the Netherlands (for example, Trappist beers).
          </p>
          <p class="text-start">
            Bottom-fermented lagers have their origins in continental Europe. Brewers in
            Plzeň (now in the Czech Republic) used local soft waters to produce the famous
            Pilsner beer, which became the standard for highly hopped, pale-coloured, dry
            lagers. Dortmunder is a pale lager of Germany, and Munich has become
            associated with dark, strong, slightly sweet beers with less hop character.
            The dark colour comes from highly roasted malt, and other characteristic
            flavours arise during the decoction mashing process. Bock is an even stronger,
            heavier Munich-type beer that is brewed in winter for consumption in the
            spring. Märzbier (“March beer”) is a lighter brew produced in the spring.
            While all German lagers are made with malted barley, a special brew called
            weiss beer (Weissbier; “white beer”) is made from malted wheat. In other
            countries such as Denmark, the Netherlands, and the United States, other
            cereals are used in lighter-coloured lager beers.
          </p>
          <p class="text-start">
            Lambic and gueuze beers are produced mainly in Belgium. The wort is made from
