            bacteria) and yeasts ferment the wort, which is high in lactic acid content.
            Lambic beer is the cask product sold locally. Gueuze is bottled and
            refermented lambic beer. Filtered gueuze, the most popular product, is a
            bottled blend of lambic and gueuze. A cask product made in a similar manner is
            thought to have been consumed by miners in the United States during the
